@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap");
body {
  margin: 0;
  padding: 0;
  background-size: cover;
  background: url(https://cdn.discordapp.com/attachments/952580552291147816/1070809270037270610/969c938b0dc6a8c6.png);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
.MuiTypography-root,
.MuiButtonBase-root,
.MuiFormLabel-root {
  color: white !important;
}

.MuiPickersDay-root,
.MuiInputLabel-filled {
  color: #000 !important;
}

.MuiInputBase-root {
  background-color: white !important;
}

a {
  text-decoration: none;
}

footer {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
}

.load {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /*change these sizes to fit into your project*/
  width: 100px;
  height: 100px;
}
.load hr {
  border: 0;
  margin: 0;
  width: 40%;
  height: 40%;
  position: absolute;
  border-radius: 50%;
  animation: spin 2s ease infinite;
}

.load :first-child {
  background: #19a68c;
  animation-delay: -1.5s;
}
.load :nth-child(2) {
  background: #f63d3a;
  animation-delay: -1s;
}
.load :nth-child(3) {
  background: #fda543;
  animation-delay: -0.5s;
}
.load :last-child {
  background: #193b48;
}

@keyframes spin {
  0%,
  100% {
    transform: translate(0);
  }
  25% {
    transform: translate(160%);
  }
  50% {
    transform: translate(160%, 160%);
  }
  75% {
    transform: translate(0, 160%);
  }
}

.timer {
  position: fixed;
  top: 30px;
  left: 30px;
  color: white;
  font-size: 20px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.center {
  text-align: center;
}

.container {
  margin: 0 30%;
}

.questions {
  text-align: left;
  display: grid;
}

.questions > div {
  margin-bottom: 20px;
}
